
import InlineLoading from 'vux/src/components/inline-loading/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: 'Scroller',
components: {
InlineLoading,
LoadMore
},
props: {
offset: {
type: Number,
default: 40
},
usePullup: {
type: Boolean,
default: false
},
usePulldown: {
type: Boolean,
default: true
}
},
data () {
return {
top: 0,
pullDownStatus: 'pull', // pull, release, loading
startY: 0,  // 保存开始滑动时，y轴位置
touching: false,
pullDownRefreshShow: true,
pullupStatus: true,
showLoadMore: false
}
},
created () {
if (this.usePulldown === false) {
this.pullDownRefreshShow = false
}
},
methods: {
touchStart (e) {

if (!this.usePulldown) return
this.startY = e.targetTouches[0].pageY
this.startScroll = this.$el.scrollTop || 0

this.touching = true
},
touchMove (e) {

if (!this.usePulldown || this.$el.scrollTop > 0 || !this.touching) {
return
}

let diff = e.targetTouches[0].pageY - this.startY - this.startScroll

if (diff > 0) e.preventDefault()

this.top = Math.pow(diff, 0.8) + (this.pullDownStatus === 'loading' ? this.offset : 0)
if (this.pullDownStatus === 'loading') {
return
}
if (this.top >= this.offset) {
this.pullDownStatus = 'release'
} else {
this.pullDownStatus = 'pull'
}
},
touchEnd (e) {
if (!this.usePulldown) return
this.touching = false
if (this.pullDownStatus === 'loading') {
this.pullDownStatus = 'loading'
this.top = this.offset
return
}
if (this.top >= this.offset) { // do pulldown
this.pulldown()
} else { // cancel pulldown
this.pullDownStatus = 'pull'
this.top = 0
}
},
pulldown () {

this.pullDownStatus = 'loading'
this.top = this.offset
this.$emit('on-pulldown-loading', this.pulldownDone)
},
pulldownDone () {
this.pullDownStatus = 'pull'
this.top = 0

},
pullup () {
this.pullupLoading = true
this.$emit('on-pullup-loading', this.pullupDone)
},
pullupDone (length) {
const self = this
if (length === 0) {

self.pullupStatus = false
}
this.showLoadMore = false
self.pullupLoading = false
},
onScroll (e) {
if (!this.usePullup || this.pullupLoading) {
return
}
let outerHeight = this.$el.clientHeight
let innerHeight = this.$el.querySelector('.scroller-inner').clientHeight
let scrollTop = this.$el.scrollTop
let headerHeight = this.usePulldown ? this.$el.querySelector('.scroller-header').clientHeight : 0
let footerHeight = this.$el.querySelector('.scroller-footer').clientHeight
let bottom = innerHeight - outerHeight - scrollTop - headerHeight
if (bottom < footerHeight) {
this.showLoadMore = true
this.pullup()
}
}
}
}



