
import { createNamespacedHelpers } from 'vuex'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Card from 'vux/src/components/card/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Badge from 'vux/src/components/badge/index.vue'

import Scroller from '../components/Scroller'
import PopupFilter from '../components/taskTrace/PopupFilter'

const { mapActions } = createNamespacedHelpers('taskTrace')

export default {
name: 'TaskTrace',
components: {
PopupFilter,
Scroller,
Tab,
TabItem,
Card,
Swiper,
SwiperItem,
Badge
},
data () {
return {
swiperIndex: 0
}
},
created () {
this.fetchSegmentListData()
},
methods: {
...mapActions(['togglePopupFilter', 'fetchSegmentList']),
handleClickFilterIcon () {
this.togglePopupFilter(true)
},
handlePulldownList (cb) {
setTimeout(() => {
cb()
}, 2000)
},
async fetchSegmentListData () {
const data = {}

this.fetchSegmentList(data)
}
}
}



