
import { createNamespacedHelpers } from 'vuex'
import Popup from 'vux/src/components/popup/index.vue'


const { mapState, mapActions } = createNamespacedHelpers('taskTrace')

export default {
name: 'PopupFilter',
components: {
Popup
},
data () {
return {
shown: false
}
},
computed: {
...mapState(['shownPopupFilter'])
},
watch: {
shownPopupFilter (newVal) {
this.shown = newVal
}
},
methods: {
...mapActions(['togglePopupFilter']),
handleHide () {
this.togglePopupFilter()
}
}
}



